import React from 'react'
import dwarves from "../images/race_dwarf.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img src={dwarves} className="race-profile"/>
            </div>
            <div className="col-lg-8">
                <h1>Dwarves</h1>
                <p>
                The <strong>Dwarves</strong> were made by Aul, whom they themselves call Mahal, meaning maker. Aul was unwilling to wait for the coming of the Children of Ilvatar, for he was impatient and desired to have someone to teach his lore and crafts. Therefore, he made the first Seven Fathers of the Dwarves in secret in a hall under the mountains of Middle-earth.

It was, however, not within Aul's power to create life. After being reprimanded by Ilvatar and realizing his error, Aul offered his creations to his father to do with as he would, including their destruction. Even as the offer was made, Ilvatar accepted and gave the Dwarves a life of their own. So when Aul picked up a great hammer to smite the Seven Fathers and destroy his presumptuous creations, they shrank back in fear and begged for mercy.

Ilvatar was however not willing to suffer that the Dwarves should come before the Firstborn (Elves), and he decreed that the Seven Fathers should sleep underground and should not come forth until the Firstborn had awakened.
                </p>
                <h1>Abilities</h1>
                <p>Due to the Dwarves high strength and very high constitution they make great warriors.</p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>High</td>
                            <td>Normal</td>
                            <td>Low</td>
                            <td>Very Low</td>
                            <td>Very High</td>
                            <td>Low</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</div>


